
.title {
    font-family: var(--second-font-bold);
    font-weight: 700;
    color: var(--main-dark-color);
    &--thin {
        font-family: var(--second-font);
        font-weight: 400; }

    &--xsmall {
        @include css-lock(14, 16, 600, 1000);
        line-height: 1.25; }

    &--small {
        @include css-lock(16, 20, 600, 1000);
        line-height: 1.4; }

    &--medium {
        @include css-lock(18, 24, 600, 1000);
        line-height: 1.58; }

    &--large {
        @include css-lock(26, 34, 600, 1000);
        line-height: 1.29; }

    &--xlarge {
        @include css-lock(36, 72, 600, 1000);
        line-height: 1.2; } }
